import React from "react";
class Comp extends React.Component {
    constructor() {
        super(...arguments);
        this.onClickSelect = (e, opt) => {
            if (e)
                e.preventDefault();
            if (!opt)
                return;
            if (typeof this.props.onClickSelect === 'function') {
                this.props.onClickSelect(opt);
            }
        };
    }
    render() {
        const props = this.props;
        const keyword = (props.keyword || '').toString().trim().toLowerCase();
        const regexp = new RegExp(keyword);
        const matchReg = (text) => {
            text = (text || '').toString().trim().toLowerCase();
            return regexp.test(text);
        };
        //type IinputProps=React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;
        let idx_name = 0;
        const inputs = [];
        return (React.createElement("div", { className: `SelectDropDown`, onBlurCapture: props.onBlur },
            React.createElement("div", { className: "SelectItems" }, props.options.map((r, idx) => {
                let match = (keyword.length < 1) || (matchReg(r.id) || matchReg(r.__label));
                if (!match)
                    return null;
                const selected = Array.isArray(props.selectedValues) && props.selectedValues.indexOf(r.__id) >= 0;
                if (selected) {
                    let name = props.name;
                    if (props.multiple) {
                        name += `[${idx_name}]`;
                    }
                    inputs.push(React.createElement("input", { name: name, value: r.__id, type: "hidden", style: { display: "none" } }));
                    idx_name++;
                }
                return (React.createElement("div", { onClick: (e) => { this.onClickSelect(e, r); }, className: `SelectItem ${selected ? 'selected' : ''} `, "data-value": `${r.__id}`, dangerouslySetInnerHTML: { __html: r.__html } }));
            })),
            inputs));
    }
}
export default Comp;
